>PTC</span><span style="font-size:9px;"></span><a name="undefined"></a><span style="font-size:9px;"></span><span style="font-size:9px;">BSC</span><span style="font-size:9px;"></span><span style="font-size:9px;">BEP1155</span><span style="font-size:9px;"></span><span style="font-size:9px;">NFT</span><span style="font-size:9px;"></span><span style="font-size:9px;">PTC</span><span style="font-size:9px;"></span><span style="font-size:9px;">NFT</span><span style="font-size:9px;"></span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;"></span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;"></span><span style="font-size:9px;">PTC</span><span style="font-size:9px;"></span><span style="font-size:9px;">BSC</span><span style="font-size:9px;"></span><span style="font-size:9px;">BEP20</span><span style="font-size:9px;"></span><span style="font-size:9px;">DEX</span><span style="font-size:9px;"></span><span style="font-size:9px;">DEX</span><span style="font-size:9px;"></span><span style="font-size:9px;">PTC</span><span style="font-size:9px;"></span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;"></span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021</span><span style="font-size:9px;">12</span><span style="font-size:9px;"> &nbsp;</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021</span><span style="font-size:9px;">12</span><span style="font-size:9px;"> </span><b> </b><span style="font-size:9px;"></span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021</span><span style="font-size:9px;">12</span><span style="font-size:9px;"> &nbsp;</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2021</span><span style="font-size:9px;">12</span><span style="font-size:9px;"> &nbsp;</span><b><span></span></b> </p><p class="MsoNormal"><span style="font-size:9px;">2021</span><span style="font-size:9px;">12</span><span style="font-size:9px;"> &nbsp;</span><b><span></span></b> </p><p class="MsoNormal"><span style="font-size:9px;">2022</span><span style="font-size:9px;">1</span><span style="font-size:9px;">  &nbsp;</span><span></span> </p><p class="MsoNormal"><span style="font-size:9px;">2022</span><span style="font-size:9px;">1</span><span style="font-size:9px;">  &nbsp;</span><span></span> </p><p class="MsoNormal"><br /></p><p class="MsoNormal" style="text-align:center;"><span style="font-size:9px;"></span><span style="font-size:9px;">PotatomanorFund</span><span style="font-size:9px;"></span> </p><p class="MsoNormal"><br /></p>',
      actions : [
        { text: '' },
        { text: 'English' }
      ],
      onSelect: (val) => {
        let lang = val.text === 'English' ? 'en' : 'zh-tw'
        setLang(lang)
        data.local = lang
        location.reload()
      },
      initContract: () => {
        data.OpenContract = new data.web3.eth.Contract(openAbi, config.OpenAddress)
        data.UsdtContract = new data.web3.eth.Contract(tokenAbi, config.UsdtAddress)
        data.initData()
      },
      initData: () => {
        data.shareUrl = `${window.location.origin}${window.location.pathname}#/?intro=${data.address}`
        data.getopenUsdt()
        data.getTokenAllowance()
        data.getAllFarmType()
      },
      getTokenAllowance: async () => {
        let wResult = await data.UsdtContract.methods.allowance(data.address, config.OpenAddress).call();
        data.NewAllowance = wResult;
      },
      approve: async () => {
        if (!data.address) {
          Toast(t('game.'))
          return
        }
        data.allowance = await data.UsdtContract.methods.approve(config.OpenAddress, MaxUint256).send({from: data.address}).then(() => {
          Toast(t('game.'))
        })
        data.getTokenAllowance()
      },
      getopenUsdt: async() => {
        data.UsdtBlance = await data.UsdtContract.methods.balanceOf(data.address).call()
        data.decimals = await data.UsdtContract.methods.decimals().call();
      },
      buyCombo: async(price, id) => {
        if (!data.address) {
          Toast(t('game.'))
          return
        }
        if(Number(price) > Number(data.UsdtBlance)) {
          Toast('USDT')
          return
        }
        let gas = await data.OpenContract.methods.buyCombo(id, data.intro).estimateGas({from: data.address})
        await data.OpenContract.methods.buyCombo(id, data.intro).send({from: data.address, gas: gas}).then((res) => {
          Toast('')
          emit('getAllCombo')
        })
      },
      getComboNumber: async() => {
        let allcomboType = []
        for(var i = 0; i < data.allcombo.length; i++) {
          allcomboType.push({ reference: 'allcomboType', methodName: 'userComboAmount', methodParameters: [data.address, data.allcombo[i].id]});
        }
        const comboCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allcomboType
          }
        ]
        const multicallCom = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsCombo = await multicallCom.call(comboCallContext);
        let comboArr = resultsCombo.results.OpenContract.callsReturnContext
        data.Newcombo = []
        for(var a = 0; a < comboArr.length; a++) {
          data.Newcombo.push(data.web3.utils.hexToNumber(comboArr[a].returnValues[0].hex))
        }
        data.usrPotato = Number(data.Newcombo[0] * 200) + Number(data.Newcombo[1] * 400) + Number(data.Newcombo[2] * 800)
      },
      getAllFarmType: async() => {
        data.allFarm = await data.OpenContract.methods.getAllFarmType().call();
        let allFarmCalls = [];
        for(var i = 1; i <= Number(data.allFarm.length); i++){
          allFarmCalls.push({ reference: 'allFarmNum', methodName: 'balanceOf', methodParameters: [data.address, i]});
        }
        const NumCallContext = [
          {
            reference: 'OpenContract',
            contractAddress: config.OpenAddress,
            abi: openAbi,
            calls: allFarmCalls
          }
        ]
        const multicallNum = new Multicall({ multicallCustomContractAddress: config.multicallAddress, web3Instance: data.web3, tryAggregate: false });
        const resultsNumber = await multicallNum.call(NumCallContext);
        let NumberArr = resultsNumber.results.OpenContract.callsReturnContext
        data.NewNUmber = []
        for(var a = 0; a < NumberArr.length; a++) {
          data.NewNUmber.push(data.web3.utils.hexToNumberString(NumberArr[a].returnValues[0].hex))
        }
      },
      handleToChart: () => {
        let domToTop = document.getElementById("mechan").offsetTop
        window.scrollTo({
        'top': domToTop,
        'behavior': 'smooth'
        })
      }
    })
    if (data.address) {
      data.initContract()
    }
    data.clipboard = new Clipboard('.clip-button')
    data.clipboard.on('success', function (e) {
      Toast(t('index.!'))
    })
    data.clipboard.on('error', function () {
      Toast(t('index.'))
    })
    onBeforeUnmount(() => {
      if(data.clipboard){
        data.clipboard.destroy();
      }
    })
    watch(() => data.allcombo, (val) => {
      if (val) {
        data.getComboNumber()
      }
    })
    watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>
